import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
})
export class HomeComponent implements OnInit {
  errorMessage: string;
  constructor( ) {}

  ngOnInit(): void {
    this.loadScript();
  }
  public loadScript() {
    const body = document.body as HTMLDivElement;
    const script = document.createElement('script');
    script.innerHTML = '';
    script.src = '/customscript.js';
    script.async = true;
    script.defer = true;
    body.appendChild(script);

    const scriptCarosel = document.createElement('script');
    scriptCarosel.innerHTML = '';
    scriptCarosel.src = '/carousel.js';
    scriptCarosel.async = true;
    scriptCarosel.defer = true;
    body.appendChild(scriptCarosel);
}
}
